        <el-form label-width="120px">
          <el-form-item label="原始ID:">
            <el-input :value="formatId(analysisResult.id)" readonly></el-input>
          </el-form-item>
          <el-form-item label="二进制:">
            <el-input :value="analysisResult.binary" readonly type="textarea" :rows="2"></el-input>
          </el-form-item>
          <el-form-item label="时间戳:">
            <span>{{ analysisResult.timestamp }} ({{ formatTime(analysisResult.timestamp) }})</span>
          </el-form-item>
          <el-form-item label="数据中心ID:">
            <span>{{ analysisResult.datacenterId }}</span>
          </el-form-item>
          <el-form-item label="机器ID:">
            <span>{{ analysisResult.machineId }}</span>
          </el-form-item>
          <el-form-item label="序列号:">
            <span>{{ analysisResult.sequence }}</span>
          </el-form-item>
          <el-form-item label="生成时间:">
            <span>{{ formatTime(analysisResult.timestamp) }}</span>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'SnowflakeIdView',
  metaInfo: {
    title: '雪花ID生成器 - 分布式唯一ID生成工具',
    meta: [
      { name: 'description', content: '雪花算法ID生成器，用于生成分布式系统中的全局唯一ID，支持自定义机器ID和数据中心ID。' },
      { name: 'keywords', content: '雪花ID,分布式ID,唯一ID生成器,Snowflake算法,全局ID' }
    ]
  },
  data() {
    return {
      machineId: 1,
      datacenterId: 1,
      generateCount: 10,
      outputFormat: 'decimal',
      generatedIds: [],
      sequence: 0,
      lastTimestamp: 0,
      showAnalysisDialog: false,
      analysisResult: null,
      stats: {
        totalGenerated: 0,
        maxSequence: 0,
        generateRate: 0,
        timeSpan: '0秒'
      },
      startTime: null,
      
      // 雪花算法常量
      EPOCH: 1288834974657, // Twitter雪花算法的起始时间戳
      MACHINE_ID_BITS: 5,
      DATACENTER_ID_BITS: 5,
      SEQUENCE_BITS: 12,
      MAX_MACHINE_ID: (1 << 5) - 1,
      MAX_DATACENTER_ID: (1 << 5) - 1,
      MAX_SEQUENCE: (1 << 12) - 1,
      MACHINE_ID_SHIFT: 12,
      DATACENTER_ID_SHIFT: 17,
      TIMESTAMP_SHIFT: 22
    }
  },
  methods: {
    generateIds() {
      if (!this.startTime) {
        this.startTime = Date.now()
      }
      
      this.generatedIds = []
      
      for (let i = 0; i < this.generateCount; i++) {
        const id = this.generateSnowflakeId()
        this.generatedIds.push(id)
      }
      
      this.updateStats()
      this.$message.success(`成功生成 ${this.generateCount} 个雪花ID`)
    },

    generateSingle() {
      if (!this.startTime) {
        this.startTime = Date.now()
      }
      
      const id = this.generateSnowflakeId()
      this.generatedIds.unshift(id)
      
      // 限制显示数量
      if (this.generatedIds.length > 1000) {
        this.generatedIds = this.generatedIds.slice(0, 1000)
      }
      
      this.updateStats()
    },

    generateSnowflakeId() {
      let timestamp = Date.now()
      
      // 如果当前时间小于上次生成时间，说明系统时钟回退了
      if (timestamp < this.lastTimestamp) {
        throw new Error('系统时钟回退，无法生成ID')
      }
      
      // 如果是同一毫秒内生成的，则序列号递增
      if (timestamp === this.lastTimestamp) {
        this.sequence = (this.sequence + 1) & this.MAX_SEQUENCE
        
        // 序列号溢出，等待下一毫秒
        if (this.sequence === 0) {
          timestamp = this.waitNextMillis(this.lastTimestamp)
        }
      } else {
        this.sequence = 0
      }
      
      this.lastTimestamp = timestamp
      
      // 计算ID
      const id = ((timestamp - this.EPOCH) << this.TIMESTAMP_SHIFT) |
                 (this.datacenterId << this.DATACENTER_ID_SHIFT) |
                 (this.machineId << this.MACHINE_ID_SHIFT) |
                 this.sequence
      
      return {
        id: id,
        timestamp: timestamp,
        datacenterId: this.datacenterId,
        machineId: this.machineId,
        sequence: this.sequence
      }
    },

    waitNextMillis(lastTimestamp) {
      let timestamp = Date.now()
      while (timestamp <= lastTimestamp) {
        timestamp = Date.now()
      }
      return timestamp
    },

    formatId(id) {
      switch (this.outputFormat) {
        case 'hex':
          return '0x' + id.toString(16).toUpperCase()
        case 'binary':
          return '0b' + id.toString(2).padStart(64, '0')
        default:
          return id.toString()
      }
    },

    formatTime(timestamp) {
      return new Date(timestamp).toLocaleString('zh-CN')
    },

    updateStats() {
      this.stats.totalGenerated = this.generatedIds.length
      this.stats.maxSequence = Math.max(this.stats.maxSequence, this.sequence)
      
      const elapsed = (Date.now() - this.startTime) / 1000
      this.stats.generateRate = Math.round(this.stats.totalGenerated / elapsed)
      this.stats.timeSpan = elapsed < 60 ? `${elapsed.toFixed(1)}秒` : `${Math.floor(elapsed / 60)}分${Math.floor(elapsed % 60)}秒`
    },

    analyzeId(idInfo) {
      this.analysisResult = {
        ...idInfo,
        binary: '0b' + idInfo.id.toString(2).padStart(64, '0')
      }
      this.showAnalysisDialog = true
    },

    async copyId(id) {
      try {
        const success = await ClipboardUtil.copyText(id)
        if (success) {
          this.$message.success('ID已复制到剪贴板')
        } else {
          this.$message.error('复制失败')
        }
      } catch (error) {
        this.$message.error('复制失败: ' + error.message)
      }
    },

    async copyAllIds() {
      if (this.generatedIds.length === 0) {
        this.$message.warning('没有可复制的ID')
        return
      }

      try {
        const allIds = this.generatedIds.map(idInfo => this.formatId(idInfo.id)).join('\n')
        const success = await ClipboardUtil.copyText(allIds)
        if (success) {
          this.$message.success('所有ID已复制到剪贴板')
        } else {
          this.$message.error('复制失败')
        }
      } catch (error) {
        this.$message.error('复制失败: ' + error.message)
      }
    },

    exportIds() {
      if (this.generatedIds.length === 0) {
        this.$message.warning('没有可导出的ID')
        return
      }

      const data = {
        config: {
          machineId: this.machineId,
          datacenterId: this.datacenterId,
          outputFormat: this.outputFormat
        },
        ids: this.generatedIds.map(idInfo => ({
          id: this.formatId(idInfo.id),
          timestamp: idInfo.timestamp,
          time: this.formatTime(idInfo.timestamp),
          sequence: idInfo.sequence
        })),
        stats: this.stats,
        exportTime: new Date().toISOString()
      }

      const filename = DownloadUtil.generateTimestampFilename('雪花ID', 'json')
      DownloadUtil.downloadJSON(data, filename)
      this.$message.success('ID数据导出成功')
    },

    clearResults() {
      this.generatedIds = []
      this.sequence = 0
      this.lastTimestamp = 0
      this.stats = {
        totalGenerated: 0,
        maxSequence: 0,
        generateRate: 0,
        timeSpan: '0秒'
      }
      this.startTime = null
    }
  }
}
</script>

